import './App.css';
import './Modal.css'

import React, { Component } from 'react';
import firebase from './firebase.js';

import Post from './components/Posts.js';
import NewForm from './components/NewForm.js';

// click for new post:
//  x new post form expands from button -> form component
//    o add transitions
//  x onClick newButton trigger function to intake user data
//  x Unsplash API call made when title loses focus after text input
//  x use 'novalidate' and 'required' to set required fields with custom error messages
//  x onClick delete: clear form and close form
//  x onClick post: add date and time to data, push user data to firebase
// likeCounter: 
//  x increment on click
//  x push value to firebase
//  x use localStorage to lock one like per user



class App extends Component {
  constructor() {
    super();
    this.state = {
      posts: [],
      showNew: false,
      touchscreen: false
    }
  }

  componentDidMount() {
    const dbRef = firebase.database().ref();
    dbRef.on("value", res => {
      const newState = [];
      const data = res.val();
      for (let el in data) {
        newState.unshift({
          key: el,
          content: data[el]
        })
      }
      this.setState({
        posts: newState
      })
    });
    // check if device is using touchscreen (no access to bandcamp's embed codes when using bandcamp.com on touch enabled/mobile devices)
    if (('ontouchstart' in window) || (navigator.maxTouchPoints > 0) || (navigator.msMaxTouchPoints > 0)) this.setState({ touchscreen: true })
  }

  savePost = (userInput) => {
    const { title, author, descript, songList, banner } = userInput;
    const currentDate = new Date();
    const publishDate = `${currentDate.getFullYear()}-${("0" + (currentDate.getMonth() + 1)).slice(-2)}-${("0" + currentDate.getDate()).slice(-2)}`
    let firebaseObj = {
      header: {
        author,
        title,
        publishInfo: publishDate,
        hearts: 0
      },
      description: descript,
      songList,
      banner
    }
    console.log(songList);
    console.log(firebaseObj);
    const dbRef = firebase.database().ref();
    // dbRef.push(firebaseObj);
    this.setState({
      showNew: false
    })
  }

  render() {
    return (
      <div className="App">
        <h1 className="block">fresh vibes</h1>

        {this.state.touchscreen ? "" :
          !this.state.showNew
            ? <button className="block newBtn" onClick={() => this.setState({ showNew: true })}>+ New</button>
            : <NewForm
              closeForm={() => this.setState({ showNew: false })}
              handleSubmit={this.savePost}
            />}

        {this.state.posts.map(el => {
          const { header, description, songList, banner } = el.content;
          return (
            <Post
              key={el.key}
              id={el.key}
              header={header}
              description={description}
              songList={songList}
              banner={banner}
            />
          )
        })}

      </div>
    );
  }
}

export default App;
